z-index: 2,
sm: ( 182, 215, 206, 37 ),
md: ( 174, 205, 448, 348 ),
xl: ( 357, 421, 784, 536 ),
),
// Image 3
3: (
rotate: -4.53,
z-index: 3,
sm: ( 165, 220, 374, -7 ),
md: ( 208, 277, 590, 170 ),
xl: ( 428, 568, 1065, 257 ),
),
// Image 4
4: (
