.modal {
  @apply transition-all;
  @apply fixed w-screen h-full top-0 left-0 flex items-center justify-center;
  @apply opacity-0 pointer-events-none invisible;
  @include z-index(modal-base);

  &.is-active {
    @apply pointer-events-auto opacity-100 visible;
  }

  .modal-overlay {
    @apply absolute w-full h-full bg-teal-dark opacity-30 z-40;
  }

  &.team {
    .modal-overlay {
      @apply bg-white;
      opacity: 8%;
    }
  }

  .modal-container {
    @apply bg-white w-full h-full z-50 overflow-y-auto overflow-x-hidden relative flex flex-col;
    @apply pt-px100;

    @screen md {
      @apply pt-px57;
    }

    @screen xl {
      @apply pt-px84;
    }
  }

  .modal-close {
    @apply absolute top-0 right-0 py-2 px-3 rounded-px10 self-end mr-5 mt-5 mb-10;
    @apply bg-teal-dark text-white;
    @apply w-px45 h-px45 rounded-px18;

    @screen md {
      @apply top-px37;
    }

    @screen xl {
      @apply top-px20 w-px50 h-px50;
    }
  }

  .modal-content-wrap {
    @apply h-full;
  }

  body.is-rc & {
    .modal-close {
      @apply bg-indigo;
    }
  }

  // todo: move this
  .team-member {
    &__details {
      span:not(:first-child) {
        @apply pl-px12 ml-px12 border-l-2 border-blue_green border-opacity-40;
      }
    }

    &__thumbnail-wrap {
      max-width: rem-calc(319px);
      @apply mx-auto flex;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }

      @screen md {
        max-width: rem-calc(210px);
        @apply float-left mr-px26;
      }

      @screen xl {
        max-width: rem-calc(236px);
      }
    }
  }



  @screen md {
    @apply w-full;

    .modal-close {
      @apply mt-0 mr-7;
    }

    .modal-container {
      @apply w-auto h-auto;
      max-width: rem-calc(668px);
      max-height: 75%;
      @apply shadow-lg rounded-px10 mx-auto;
    }
  }

  @screen lg {
    .modal-close {
      @apply mr-5 -mb-4;
    }
  }

  @screen xl {
    .modal-container {
      max-width: rem-calc(1028px);

      .prose {
        @apply max-w-full;
      }
    }
  }
}

body.modal-active {
  overflow-x: hidden;
  overflow-y: visible !important;

  .site-content {
    filter: url('#blur3px');
  }
}
